.workout-selector {
  padding: 16px;
  max-width: 100%;
  background: #ffffff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);

  .selector-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
    padding-bottom: 12px;
    border-bottom: 1px solid #e0e0e0;

    .title {
      display: flex;
      align-items: center;
      gap: 8px;
      margin: 0;
      font-size: 1.2rem;
      font-weight: 600;
      color: #333;

      mat-icon {
        color: #1976d2;
      }
    }

    .header-actions {
      display: flex;
      align-items: center;
      gap: 12px;

      .selection-count {
        font-size: 0.9rem;
        color: #666;
        font-weight: 500;
      }

      .clear-btn {
        min-width: auto;
        padding: 6px 12px;
      }

      .sync-btn {
        min-width: auto;
        padding: 8px 16px;
      }
    }
  }

  .error-message {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 12px;
    background: #ffebee;
    border: 1px solid #f44336;
    border-radius: 4px;
    margin-bottom: 16px;
    color: #c62828;
    font-size: 0.9rem;

    mat-icon {
      font-size: 20px;
      width: 20px;
      height: 20px;
    }
  }

  .loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 40px 20px;
    gap: 16px;

    span {
      color: #666;
      font-size: 1rem;
    }
  }

  .empty-state {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 40px 20px;
    text-align: center;

    .empty-icon {
      font-size: 64px;
      width: 64px;
      height: 64px;
      color: #ccc;
      margin-bottom: 16px;
    }

    h3 {
      margin: 0 0 8px 0;
      color: #666;
      font-weight: 500;
    }

    p {
      margin: 0 0 24px 0;
      color: #888;
      max-width: 400px;
      line-height: 1.5;
    }
  }

  .filters-section {
    margin-bottom: 16px;

    .search-field {
      width: 100%;
    }
  }

  .activities-list {
    mat-selection-list {
      padding: 0;
      max-height: 500px;
      overflow-y: auto;

      .mat-mdc-list-item {
        height: auto;
        min-height: 120px;
        padding: 12px 16px;
        border-bottom: 1px solid #f0f0f0;

        &:hover {
          background: #fafafa;
        }

        &.mdc-list-item--selected {
          background: #e3f2fd;
          border-color: #1976d2;
        }

        &.mdc-list-item--disabled {
          opacity: 0.6;
        }
      }

      .activity-item {
        width: 100%;

        .activity-content {
          display: flex;
          flex-direction: column;
          gap: 12px;
          width: 100%;

          .activity-header {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            gap: 16px;

            .activity-info {
              display: flex;
              align-items: flex-start;
              gap: 12px;
              flex: 1;

              .activity-type-icon {
                color: #1976d2;
                font-size: 24px;
                width: 24px;
                height: 24px;
                margin-top: 4px;
              }

              .activity-text {
                flex: 1;

                .activity-name {
                  margin: 0 0 4px 0;
                  font-size: 1rem;
                  font-weight: 600;
                  color: #333;
                  line-height: 1.3;
                }

                .activity-meta {
                  display: flex;
                  align-items: center;
                  gap: 8px;
                  font-size: 0.85rem;
                  color: #666;

                  .activity-type {
                    font-weight: 500;
                    color: #1976d2;
                  }

                  .activity-date {
                    color: #888;
                  }
                }
              }
            }

            .activity-stats {
              display: flex;
              gap: 16px;
              flex-shrink: 0;

              .stat-item {
                display: flex;
                flex-direction: column;
                align-items: center;
                text-align: center;
                min-width: 60px;

                .stat-value {
                  font-size: 0.9rem;
                  font-weight: 600;
                  color: #333;
                  line-height: 1.2;
                }

                .stat-label {
                  font-size: 0.75rem;
                  color: #888;
                  text-transform: uppercase;
                  letter-spacing: 0.5px;
                }
              }
            }
          }

          .activity-description {
            font-size: 0.9rem;
            color: #666;
            line-height: 1.4;
            margin: 0;
            padding: 8px 0;
            border-top: 1px solid #f5f5f5;
          }

          .activity-actions {
            display: flex;
            justify-content: flex-end;
            padding-top: 8px;

            .details-btn {
              mat-icon {
                font-size: 20px;
                width: 20px;
                height: 20px;
              }
            }
          }
        }
      }
    }
  }

  .limit-message {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 12px;
    background: #e8f5e8;
    border: 1px solid #4caf50;
    border-radius: 4px;
    margin-top: 16px;
    color: #2e7d32;
    font-size: 0.9rem;

    mat-icon {
      font-size: 20px;
      width: 20px;
      height: 20px;
    }
  }
}

/* Responsive */
@media (max-width: 768px) {
  .workout-selector {
    padding: 12px;

    .selector-header {
      flex-direction: column;
      align-items: stretch;
      gap: 12px;

      .header-actions {
        justify-content: space-between;
        align-items: center;
      }
    }

    .activities-list {
      mat-selection-list {
        .activity-item {
          .activity-content {
            .activity-header {
              flex-direction: column;
              gap: 12px;

              .activity-stats {
                justify-content: space-around;
                width: 100%;

                .stat-item {
                  min-width: 50px;
                }
              }
            }
          }
        }
      }
    }
  }
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
  .workout-selector {
    background: #1e1e1e;
    color: #ffffff;

    .selector-header {
      border-bottom-color: #333;

      .title {
        color: #ffffff;
      }

      .header-actions {
        .selection-count {
          color: #ccc;
        }
      }
    }

    .activities-list {
      mat-selection-list {
        .mat-mdc-list-item {
          border-bottom-color: #333;

          &:hover {
            background: #2a2a2a;
          }

          &.mdc-list-item--selected {
            background: #1a237e;
          }
        }

        .activity-item {
          .activity-content {
            .activity-header {
              .activity-info {
                .activity-text {
                  .activity-name {
                    color: #ffffff;
                  }

                  .activity-meta {
                    color: #ccc;
                  }
                }
              }

              .activity-stats {
                .stat-item {
                  .stat-value {
                    color: #ffffff;
                  }

                  .stat-label {
                    color: #aaa;
                  }
                }
              }
            }

            .activity-description {
              color: #ccc;
              border-top-color: #333;
            }
          }
        }
      }
    }
  }
}